/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
1200 - 1800px:  Standard desktop
1800+ px:       Large desktop
  
$breakpoint argument choices"
- phone
- tab-port
- tab-land
- lg-desktop

1em = 16px
*/
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

footer {
  text-align: center;
  color: #fff; }

@media (max-width: 37.5em) {
  h1 {
    font-size: 1.6rem; } }

@media (max-width: 37.5em) {
  .lead {
    font-size: 1.1em; } }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 5rem;
  min-height: 100vh;
  box-sizing: border-box;
  scroll-behavior: smooth;
  background: -webkit-gradient(linear, left top, right bottom, from(#8e2de2), to(#4a00e0));
  background: linear-gradient(to right bottom, #8e2de2, #4a00e0);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  @media (max-width: 37.5em) {
    body {
      padding: 0.5rem; } }

footer {
  max-width: 50vw;
  margin: 2rem auto 5rem; }

.App {
  text-align: center;
  max-width: 40rem;
  margin: auto;
  background-color: #fff;
  padding: 5rem;
  border-radius: 0.3rem; }
  @media (max-width: 37.5em) {
    .App {
      padding: 1.5rem; } }

.ma-top-sm {
  margin-top: 1.5rem; }

.ma-top-md {
  margin-top: 3rem; }
  @media (max-width: 37.5em) {
    .ma-top-md {
      margin-top: 1.5rem; } }

.ma-top-lg {
  margin-top: 6rem; }
  @media (max-width: 37.5em) {
    .ma-top-lg {
      margin-top: 1.5rem; } }

.ma-bot-sm {
  margin-bottom: 1.5rem; }

.ma-bot-md {
  margin-bottom: 3rem; }

.ma-bot-lg {
  margin-bottom: 6rem; }

.ma-right-sm {
  margin-right: 1.5rem; }

.list-group-item {
  display: flex;
  align-items: center; }
  .list-group-item:hover {
    background-color: #f8f9fa; }
  .list-group-item:hover .list-group-item__label-input {
    background-color: #f8f9fa; }
  .list-group-item__checkbox {
    margin-right: 1rem; }
  .list-group-item__label {
    flex-grow: 1;
    display: flex;
    margin-bottom: 0; }
  .list-group-item__label-input {
    border: none;
    flex-grow: 1;
    margin-right: 1rem; }
    .list-group-item__label-input:focus {
      outline: none; }
  .list-group-item__label-input--selected {
    color: #ccc;
    text-decoration: line-through;
    font-style: italic; }
  .list-group-item__remove {
    cursor: pointer; }

.form-inline--flex-end {
  justify-content: flex-end; }

.form-group--inline-extends {
  flex-grow: 1 !important;
  margin-right: 2rem; }
  @media (max-width: 37.5em) {
    .form-group--inline-extends {
      width: 100%;
      margin-right: 0; } }

.form-control--inline-extends {
  width: 100% !important; }

.form-check--aligned {
  margin-left: 1.3rem; }

.task-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center; }
